<template>
  <div class="user-manager">
    <div class="main">
      <div class="controller-title">用户管理</div>
      <div class="content">
        <el-table :data="user_grounp" style="width: 100%">
          <el-table-column prop="id" label="序号" width="150">
          </el-table-column>
          <el-table-column prop="username" label="昵称" width="180">
          </el-table-column>
          <el-table-column prop="phone" label="电话">
          </el-table-column>
          <el-table-column prop="recently_landed" label="最近登录时间">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'UserManager',
    data() {
      return {
        url: '/user',
        user_grounp: []
      }
    },
    created() {
      this.getAllUser();
    },
    methods: {
      // 获取所有用户信息
      getAllUser: function() {
        this.$axios.get(this.url + '/queryAllUser')
          .then(res => {
            if (res.data.code == 200) {
              this.user_grounp = res.data.body;
            } else {
              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }
          })
      }
    }
  }
</script>

<style>
</style>



